import React from "react";
import {getInstancePlayer, getWeb3}  from "../Web3Util";
import blockchain from "../Web3Util";
import Web3 from 'web3';

import Button from '@material-ui/core/Button';
import Input from '@material-ui/core/Input';

import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';

class HomePage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      hasHomepageData: false,
      isLoadingHomepageData:true,
      lastRequestedAddress:"",
      requestedPrice:0,
      requestedTime:0,
      requestURL:"",
      songCounter:0,
      requestedTime:0,
      requestedAddress:"",
      requestedUrl:""
    };
    this.requestSong = this.requestSong.bind(this)
    this.fetchBlockchainData = this.fetchBlockchainData.bind(this)
  }

  componentDidMount = async () => {
    try{
      const web3 = await getWeb3();
      window.web3 = web3;
      const contractInstancePlayer = await getInstancePlayer(window.web3);

      this.setState({ user: window.user });
      this.setState({ contractInstancePlayer});
      await this.loadData();
    } catch (e) {
      console.log('Error', e)
    }finally{
      this.setState({
        isLoadingHomepageData:false
      })
    }
    this.interval = setInterval(() =>  this.fetchBlockchainData(), 1000);

  }
  componentWillUnmount() {
    if (this.interval) {
      clearInterval(this.interval);
    }
  }
  async fetchBlockchainData() {
    console.log("aqui")

    try {
      let requestedPrice = await this.state.contractInstancePlayer.methods._price().call()
        let requestedTime = await this.state.contractInstancePlayer.methods._requestedTime().call()
        let songCounter = await this.state.contractInstancePlayer.methods._songCounter().call()
        let requestedAddress = await this.state.contractInstancePlayer.methods._requestedAddress().call()
        let requestedUrl = await this.state.contractInstancePlayer.methods._requestedUrl().call()
        requestedUrl = requestedUrl.replace("watch?v=","/embed/")
        if(songCounter != this.state.songCounter){
          this.setState({requestedPrice,requestedTime,songCounter,requestedAddress,requestedUrl})
        }else{
          this.setState({requestedTime})
        }
    } catch (e) {

      console.log('Error', e)

    }
  }
  async loadData() {
      try {
        let requestedPrice = await this.state.contractInstancePlayer.methods._price().call()
        let requestedTime = await this.state.contractInstancePlayer.methods._requestedTime().call()
        let songCounter = await this.state.contractInstancePlayer.methods._songCounter().call()
        let requestedAddress = await this.state.contractInstancePlayer.methods._requestedAddress().call()
        let requestedUrl = await this.state.contractInstancePlayer.methods._requestedUrl().call()
        requestedUrl = requestedUrl.replace("watch?v=","/embed/")
        
        this.setState({hasHomepageData:true,requestedPrice,requestedTime,songCounter,requestedAddress,requestedUrl})
    } catch (e) {
      console.log('Error', e)
    }
  }

  async requestSong(){
    try {
      this.state.contractInstancePlayer.methods.requestSong(this.state.requestURL).send({
        value:this.state.requestedPrice
      })
      await this.loadData()
    } catch (e) {
      console.log('Error', e)
    } 
  }

   timeSince(date) {

    var seconds = Math.floor((new Date() - date*1000) / 1000);
  
    var interval = seconds / 31536000;
  
    if (interval > 1) {
      return Math.floor(interval) + " years";
    }
    interval = seconds / 2592000;
    if (interval > 1) {
      return Math.floor(interval) + " months";
    }
    interval = seconds / 86400;
    if (interval > 1) {
      return Math.floor(interval) + " days";
    }
    interval = seconds / 3600;
    if (interval > 1) {
      return Math.floor(interval) + " hours";
    }
    interval = seconds / 60;
    if (interval > 1) {
      return Math.floor(interval) + " minutes";
    }
    return Math.floor(seconds) + " seconds";
  }
  render() {
    return(
          <Container  style={{textAlign:"center"}}>
            <h1>Crypto Youtube Player</h1>

            <br />

            <Grid container spacing={2} >
              <Grid item xs={12} md={6} style={{alignItems:"center",display:"flex",justifyContent:"center"}}>
                <div>
                  <div style={{textAlign:"center"}}>
                    <br />
                    {
                    this.state.isLoadingHomepageData
                    ? null

                    :
                    <iframe width="360" allow='autoplay' height="315" src={this.state.requestedUrl+"?autoplay=1&loop=1"} title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>                  

                    }
                    </div>
                </div>
              </Grid>
              <Grid item xs={12} md={6} style={{alignItems:"center",display:"flex",justifyContent:"center"}}>
                <div>
                  <div style={{textAlign:"center"}}>
                    <br />
                    Song counter: {this.state.songCounter} songs<br />
                    Last requested address: {this.state.requestedAddress}<br />
                    Last song requested since: {this.timeSince(this.state.requestedTime)} ago<br />
                  </div>
                </div>
              </Grid>
            <p style={{textAlign:"center",width:"100%"}}>Pay {Web3.utils.fromWei(this.state.requestedPrice+"")}  {blockchain()} to play your song. Every time a new song plays, {Web3.utils.fromWei(this.state.requestedPrice+"")*9/10}  {blockchain()} goes to the last person that requested a song.</p>
            <p style={{textAlign:"center",width:"100%"}}> The first 10.000 to request a song get a Crypto Player NFT.</p>
              {window.user != undefined?
              <>
                <Grid item xs={12} md={12} style={{alignItems:"center",display:"flex",justifyContent:"center", padding:0}}>
                  <div>
                    <Input onChange={e => this.setState({ requestURL: e.target.value })} value={this.state.requestURL} style={{width:"100%"}} placeholder="Enter youtube url: (example https://www.youtube.com/watch?v=sadJYWbffdGTe)" />
                    <Button onClick={(event)=>{this.requestSong(event)}} >Confirm song request for {Web3.utils.fromWei(this.state.requestedPrice+"")}  ${blockchain()}</Button> 
                  </div>
                </Grid>
              </>
            :
            <Grid item xs={12} md={12} style={{alignItems:"center",display:"flex",justifyContent:"center"}}>
                
                <b>Connect your wallet to the website to submit your song for {Web3.utils.fromWei(this.state.requestedPrice+"")} ${blockchain()}</b>

            </Grid>
            }
            <br />
            <br />

         
            </Grid>
            
          </Container>
       
    )
  }
}

export default HomePage;
